.main {
    height: 100%;
    background-color: black;
    display: flex;
}

.header {
    height: 45vw;
    width:90%;
    margin: 30px auto;
    background: url('../../assets/header_img.jpg') no-repeat center;
    background-size: contain;
    position: relative;
    border-radius: 50px;

}
.header-contents{
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items:   start;
    text-align: start;
    gap: 1.5vw;
    max-width: 50%;
    bottom: 40%;
    left: 8vw;
    animation: fadeIn 3s;
}
.header-contents h2{
    font-weight: 500;
    color: white;
    font-size: max(4.5vw,22px);
   
    
    
}

.header-contents button{
    border: none;
    color: black;
    padding: 1vw 2.3vw;
    font-weight: 500;
    background-color: white;
    font-size: max(1vw,13px);
    border-radius:50px;
    margin-left: 0;
    cursor: pointer;
    border-color: black;
    border: 1px solid black;
    margin-top: -50px;
    transition: all 0.3s;
    
}
.header-contents button:hover{
    background-color: goldenrod;
    color: white;
    transform: scale(1.1);
}
@media (max-width: 1050px) {
    .header-contents {
        max-width: 45%;
    }
    .header-contents button {
        padding:1vw 2vw;
        margin-top: -10px;
       
    }

}
@media (max-width: 750px) {
    .header {
        height: 60vw;
        background-size: cover; 
    }

    .header-contents {
        max-width: 65%;
        bottom: 20%; 
        left: 5vw;
    }

    .header-contents h2 {
        font-size: max(6vw, 18px); 
    }

    .header-contents p {
        display: none;
    }

    .header-contents button {
        padding: 2vw 4vw;
        font-size: max(2vw, 12px); 
    }
}
@media (max-width: 480px) {
    .header {
        height: 70vw; 
    }

    .header-contents {
        max-width: 80%;
        bottom: 10%; 
        left: 5vw;
    }

    .header-contents h2 {
        font-size: max(8vw, 16px); 
    }

    .header-contents button {
        padding: 3vw 5vw;
        font-size: max(3vw, 10px); 
        margin-top: 10px;
    }
}